<template>
  <div class="about">
    <h1>This is an about page</h1>
  </div>
</template>

<script>
import {mapState} from 'vuex';

export default {
  computed: {
    ...mapState(['token'])
  },
  methods:{
    async dataProtected(){
      try{
        const res = await fetch('https://api-jwt-node.herokuapp.com/api/admin',{
          headers: {
            'Content-Type': 'application/json',
            'auth-token': this.token
          }
        })

        const data = await res.json();
        
        console.log(data);
      }catch(error){
        console.log(error)
      }
    }
  },
  created(){
    this.dataProtected();
  }

}
</script>
